import mongoose from 'mongoose'
const Schema = mongoose.Schema

const AgentDepositSchema = mongoose.Schema({
	owner:{
		player: { type: Schema.Types.ObjectId, ref: 'Player' },
		superAgent : { type: Schema.Types.ObjectId, ref: 'SuperAgent' },
		agent : { type: Schema.Types.ObjectId, ref: 'Agent' }
	},
	orderType: { type: String, default: 'Deposit' },
	creditAmount: { type: Number },
	priceAmount: { type: Number },
	orderNumber: { type: String, required: true },
	closedAt: { type: Date, default: Date.now }
})


const AgentDeposit = mongoose.model('AgentDeposit', AgentDepositSchema)

export default AgentDeposit